import { Color } from '@/app/shared/client/type/color';
import { GlobalColorsString } from '@/app/shared/client/type/global-string';

export enum OverlayType {
  SOLID = 'solid',
  GRADIENT = 'gradient',
  NONE = 'none',
}

interface SolidSettings {
  color?: Color;
}

interface GradientSettings {
  fromColor?: Color;
  toColor?: Color;
}

interface SolidDefaults {
  color?: Color | GlobalColorsString;
}

interface GradientDefaults {
  fromColor?: Color | GlobalColorsString;
  toColor?: Color | GlobalColorsString;
}

export enum ScrollEffect {
  NONE = 'none',
  PARALLAX = 'parallax',
  FIXED = 'fixed',
  ZOOM_OUT = 'zoom_out',
}

interface OverlaySettings {
  type?: OverlayType;
  solid?: SolidSettings;
  gradient?: GradientSettings;
}

interface OverlayDefaults {
  type?: OverlayType;
  solid?: SolidDefaults;
  gradient?: GradientDefaults;
}

export interface ImageSettings {
  overlay?: OverlaySettings;
  visible?: boolean;
}

export interface ScrollableImageSettings {
  overlay?: OverlaySettings;
  scrollEffect?: ScrollEffect;
  visible?: boolean;
}

export interface ImageDefaults {
  overlay?: OverlayDefaults;
  visible?: boolean;
}

export interface ScrollableImageDefaults {
  overlay?: OverlayDefaults;
  scrollEffect?: ScrollEffect;
  visible?: boolean;
}
