




















































































import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  ColorBoxEditorControl, ComboBoxEditorControl,
  CheckboxToggleEditorControl,
} from '@/app/editor/client/view/component/control';
import {
  ScrollableImageSettings,
  OverlayType, ScrollEffect, ScrollableImageDefaults,
} from '@/app/shared/client/type/settings/image-settings';
import { Color } from '@/app/shared/client/type/color';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { calculateActualColorDefault } from '@/app/editor/client/logic/defaults';
import useI18N from '@/app/shared/client/logic/i18n';

function useOverlayTypeChangeHandler(
  settings: Readonly<Ref<Readonly<ScrollableImageSettings>>>,
  emit: (event: string, arg: ScrollableImageSettings) => void,
): (value?: OverlayType) => void {
  function onOverlayTypeChange(value?: OverlayType): void {
    if (value === undefined) {
      const newSettings = {
        ...settings.value,
        overlay: undefined,
      };
      emit('editor-action', newSettings);
      return;
    }

    const newOverlay = {
      ...settings.value.overlay,
      type: value,
    };
    const newSettings = {
      ...settings.value,
      overlay: newOverlay,
    };
    emit('editor-action', newSettings);
  }

  return onOverlayTypeChange;
}

function useSolidOverlayColorChangeHandler(
  settings: Readonly<Ref<Readonly<ScrollableImageSettings>>>,
  emit: (event: string, arg: ScrollableImageSettings) => void,
): (value?: Color) => void {
  function onSolidOverlayColorChange(value?: Color): void {
    const newSolid = {
      ...settings.value.overlay?.solid,
