    emit('editor-action', newSettings);
  }

  return onScrollEffectChange;
}

function calculateOverlayTypeChoices(
  defaultType: string | undefined,
  i18n: (label: string) => string,
): Array<{ key: string | undefined, label: string }> {
  const choices = [
    { key: OverlayType.SOLID, label: i18n('CommonLabels.StandardImage.Overlay.Solid') },
    { key: OverlayType.GRADIENT, label: i18n('CommonLabels.StandardImage.Overlay.Gradient') },
    { key: OverlayType.NONE, label: i18n('CommonLabels.StandardImage.Overlay.None') },
  ];

  const defaultOverlayLabel = choices
    .find((choice) => defaultType === choice.key)
    ?.label ?? '';

  const defaultOverlayTypeText = i18n('CommonLabels.StandardImage.Overlay.Default')
    .replace('{overlayType}', defaultOverlayLabel);

  return [
    ...choices,
    { key: undefined, label: defaultOverlayTypeText },
  ];
}

const ScrollableImageDesignEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
    ColorBoxEditorControl,
    CheckboxToggleEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ScrollableImageSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<ScrollableImageDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      overlay: {
        type: props.value?.overlay?.type,
        solid: props.value?.overlay?.solid,
        gradient: props.value?.overlay?.gradient,
      },
      scrollEffect: props.value?.scrollEffect ?? props.defaults?.scrollEffect as ScrollEffect,
      visible: props.value?.visible ?? props.defaults?.visible,
    }));

    const overlayType = computed({
      get: () => settings.value.overlay.type,
      set: useOverlayTypeChangeHandler(settings, emit),
    });
    const overlayTypeChoices = computed(
      () => calculateOverlayTypeChoices(props.defaults.overlay?.type, i18n),
    );

    const defaultSolidOverlayColor = computed(() => calculateActualColorDefault(
      props.defaults.overlay?.solid?.color,
      props.siteSettings,
    ));
    const solidOverlayColor = computed({
      get: () => settings.value.overlay?.solid?.color,
      set: useSolidOverlayColorChangeHandler(settings, emit),
    });

    const defaultGradientOverlayFromColor = computed(() => calculateActualColorDefault(
      props.defaults.overlay?.gradient?.fromColor,
      props.siteSettings,
    ));
    const gradientOverlayFromColor = computed({
      get: () => settings.value.overlay?.gradient?.fromColor,
      set: useGradientOverlayFromColorChangeHandler(settings, emit),
    });

    const defaultGradientOverlayToColor = computed(() => calculateActualColorDefault(
      props.defaults.overlay?.gradient?.toColor,
      props.siteSettings,
    ));
    const gradientOverlayToColor = computed({
      get: () => settings.value.overlay?.gradient?.toColor,
      set: useGradientOverlayToColorChangeHandler(settings, emit),
    });

    const scrollEffect = computed({
      get: () => settings.value.scrollEffect,
      set: useScrollEffectChangeHandler(settings, emit),
    });
    const scrollEffectChoices = [
      {
        key: ScrollEffect.NONE,
        label: i18n('CommonLabels.FullImage.ScrollingEffect.None'),
      },
      {
        key: ScrollEffect.PARALLAX,
        label: i18n('CommonLabels.FullImage.ScrollingEffect.Parallax'),
      },
      {
        key: ScrollEffect.FIXED,
        label: i18n('CommonLabels.FullImage.ScrollingEffect.Fixed'),
      },
      {
        key: ScrollEffect.ZOOM_OUT,
        label: i18n('CommonLabels.FullImage.ScrollingEffect.Zoom'),
      },
    ];

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibleChangeHandler(settings, emit),
    });

    return {
      i18n,
      overlayType,
      overlayTypeChoices,
      defaultSolidOverlayColor,
      solidOverlayColor,
      defaultGradientOverlayFromColor,
      gradientOverlayFromColor,
      defaultGradientOverlayToColor,
      gradientOverlayToColor,
      scrollEffect,
      scrollEffectChoices,
      visibility,
    };
  },
});

export default ScrollableImageDesignEditor;
